// Подключение шрифтов
// Пример: @include font-face("Cabinet Grotesk", "/fonts/CabinetGrotesk/CabinetGrotesk-Bold", 700);
// Путь писать без расширения файла, то есть все файлы должны называться одинаково,
// Но в конце будут разные расширения: example.woff, example.woff2, example.eot
// Ещё один пример: @include font-face("saira", "/fonts/saira/saira", 400); то есть в папке saira
// лежат файлы saira.woff2, saira.eot и так далее, хватит хотя бы одного расширения и проект будет работать

@font-face {
  font-family: 'Cera Round Pro';
  font-weight: regular;
  font-style: normal;
  src: url('../fonts/CeraRoundPro/TypeMates - CeraRoundPro-Regular.otf')
    format('opentype');
}

@font-face {
  font-family: 'Cera Round Pro';
  font-weight: medium;
  font-style: normal;
  src: url('../fonts/CeraRoundPro/TypeMates - CeraRoundPro-Medium.otf')
    format('opentype');
}

@font-face {
  font-family: 'Cera Round Pro';
  font-weight: bold;
  font-style: normal;
  src: url('../fonts/CeraRoundPro/TypeMates - CeraRoundPro-Bold.otf')
    format('opentype');
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/OpenSans/OpenSans.ttf') format('truetype');
}